/*------------------------
	base class definition
-------------------------*/

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'nucleo';
  color: inherit;
  flex-shrink: 0;
  text-transform: none;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*------------------------
  change icon size
-------------------------*/

/* relative units */
.icon-sm {
  font-size: 0.8em;
}
.icon-lg {
  font-size: 1.2em;
}
/* absolute units */
.icon-16 {
  font-size: 16px;
}
.icon-32 {
  font-size: 32px;
}

/*------------------------
  spinning icons
-------------------------*/

.icon-is-spinning {
  animation: icon-spin 1s infinite linear;
}

@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/*------------------------
  rotated/flipped icons
-------------------------*/

.icon-rotate-90  {
	transform: rotate(90deg);
}

.icon-rotate-180 {
	transform: rotate(180deg);
}

.icon-rotate-270 {
	transform: rotate(270deg);
}

.icon-flip-y {
	transform: scaleY(-1);
}
.icon-flip-x {
	transform: scaleX(-1);
}

/*------------------------
	icons
-------------------------*/
@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

$icon-logo-github: unicode(ea02);
$icon-logo-linkedin: unicode(ea03);
$icon-logo-youtube: unicode(ea04);
$icon-logo-instagram: unicode(ea05);
$icon-right-arrow: unicode(ea06);

.icon-logo-github::before {
  content: $icon-logo-github;
}

.icon-logo-linkedin::before {
  content: $icon-logo-linkedin;
}

.icon-logo-youtube::before {
  content: $icon-logo-youtube;
}

.icon-logo-instagram::before {
  content: $icon-logo-instagram;
}

.icon-right-arrow::before {
  content: $icon-right-arrow;
}

/* COLORS */
$orange: #c23e27;
$blue: #0374b6;
$white: #ffffff;
$black: #1c1c1c;

/* LAYOUT */
$max-width: 1100px;

* {
  font-family: 'Helvetica Neue' serif;
	line-height: 1;
	color: $white;
}

.heading, h1, h2, h3, h4, h5,
.paragraph, p {
	line-height: 1.6;
	font-weight: 400;
}

.heading--h1 {
  font-size: 75px; 
  margin-right: 0px;
  font-family: 'Helvetica Neue';
	font-weight: 800;
	font-weight: 800;
}

.heading--h2 {
	font-size: 50px;
	font-weight: 800;
}

.heading--h3 {
	font-size: 25px;
	font-weight: 500;
}

.heading--h4 {
	font-size: 20px;
	font-weight: 500;
}

.heading--h5 {
	font-size: 20px;
}

.paragraph {
	font-size: 25px;
	font-weight: 300;
}

.link-navigation {
	font-size: 18px;
}

.input, input,
.textarea, textarea {
	font-size: 18px;
	line-height: 1.5;
}

@media (max-width: 900px) {
	.heading--h1 { font-size: 60px; }
}
@media (max-width: 750px) {
	.heading--h1 { font-size: 50px; }
}
@media (max-width: 600px) {
	.heading--h1 { font-size: 40px; }
	.heading--h2 { font-size: 40px; }
}
@media (max-width: 450px) {
	.heading--h1 { font-size: 30px; }
	.heading--h2 { font-size: 30px; }
}
@media (max-width: 350px) {
	.heading--h1 { font-size: 22px; }
	.heading--h2 { font-size: 22px; }
	.heading--h3 { font-size: 18px; }
	.heading--h4 { font-size: 18px; }
}


